<template>
    <section class="body">
        <div id="title">
            <div class="element">
                <el-link :underline="false" type="primary" id="link" @click="toMap">&lt;&nbsp;</el-link>
            </div>
            <span v-if="$store.state.problemPage == 'device'" class="element">{{ isEmpty(deviceNum) ? '' :
                `${deviceNum}`
            }}</span>
            <span v-if="$store.state.problemPage == 'device'" class="element">{{ isEmpty(stationNum) ? '' :
                `${stationNum}`
            }}</span>
            <span v-else-if="$store.state.problemPage == 'all'" class="element"></span>
            <span v-else-if="$store.state.problemPage == 'unmatch'" class="element"></span>

            <span id='account' class="element">{{ ROLE_VALUE[$store.state.role] }}
                {{ USER_VALUE[$store.state.user] }}</span>
        </div>

        <div id="operation">
            <!--  -->
            <el-tooltip class="item" effect="dark"
                :content="$store.state.problemPage == 'device' ? '' : ''"
                placement="bottom-start">
                <span class="search">
                    <el-input v-model.trim="search" class="w-50 m-2">
                        <template #prefix>
                            <el-icon class="el-input__icon">
                                <search />
                            </el-icon>
                        </template>
                    </el-input>
                </span>
            </el-tooltip>
            <el-date-picker class="datetimeRange" v-model="datetimeRange" type="datetimerange" range-separator=""
                start-placeholder="" end-placeholder="">
            </el-date-picker>
            <!--  -->
            <span class="statusFilter">
                <el-select v-model="status">
                    <el-option v-for="item in statusOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </span>

            <!--  -->
            <span class="departFilter">
                <el-select v-model="department">
                    <el-option v-for="item in departOptions" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </span>
        </div>
        <div id="table">
            <el-table :data="tableData" stripe fixed style="width: 100%" :cell-style="{ 'text-align': 'center' }"
                :header-cell-style="{ background: '#10472D', color: 'white', 'line-hight': '50px', 'text-align': 'center' }">
                <el-table-column prop="id" label="" width="120">
                </el-table-column>
                <el-table-column v-if="$store.state.problemPage != 'device'" prop="deviceNum" label="" width="120">
                </el-table-column>
                <el-table-column v-if="$store.state.problemPage != 'device'" prop="stationNum" label="" width="120">
                </el-table-column>
                <el-table-column prop="name" label="" width="120">
                </el-table-column>
                <el-table-column prop="dateCreated" label="" width="180">
                </el-table-column>
                <el-table-column prop="detail" label="" width="180">
                </el-table-column>
                <el-table-column prop="isNeedHelp" label="" width="250">
                </el-table-column>
                <el-table-column prop="picture" label="" width="180">
                    <template v-slot="{ row }">
                        <el-image style="width: 100px; height: 100px" :src="row.picture" :fit='"scale-down"'></el-image>
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="" fixed="right">
                    <template v-slot="{ row, $index }">
                        <el-button v-if="row.status == ''" type="success" @click="handleFinished(row.id, $index)"
                            :disabled="$store.state.user != 'root'">{{
                                row.status }}</el-button>
                        <el-button v-else-if="row.status == ''" type="warning" @click="handleReview(row.id, $index)">{{
                            row.status }}</el-button>
                        <el-button v-else-if="row.status == ''" type="danger"
                            @click="handleUnfinished(row.id, row.isNeedHelp, $index)">{{
                                row.status }}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <div id="page">
            <span class="countTxt">{{ count }}&nbsp</span>
            <el-pagination background @current-change="setPage" :page-size="5" layout="prev, pager, next, jumper"
                :total=count>
            </el-pagination>
        </div>

    </section>
</template>

<script>
import { Search } from '@element-plus/icons-vue'
import moment from 'moment'

export default {
    name: 'ProblemsMap',
    components: {
        Search
    },
    data() {
        return {
            deviceNum: '',
            stationNum: '',
            search: '',
            statusOptions: [{
                value: 'all',
                label: ''
            }, {
                value: 'unfinished',
                label: ''
            }, {
                value: 'review',
                label: ''
            }, {
                value: 'finished',
                label: ''
            }],
            status: 'all',
            department: 'all',
            departOptions: [{
                value: 'all',
                label: ''
            }, {
                value: 'ZT1-',
                label: 'ZT1-'
            }, {
                value: 'ZT2-',
                label: 'ZT2-'
            }, {
                value: 'ZT3-',
                label: 'ZT3-'
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }],
            tableData: [],
            count: 0,
            page: 0,
            ROLE_VALUE: {
                'root': '',
                'assist': '',
                'local': ''
            },
            USER_VALUE: {
                'root': 'root',
                'zt1': 'ZT1-',
                'zt2': 'ZT2-',
                'zt3': 'ZT3-',
                'improve': '',
                'provide': '',
                'assy': '',
                'logistics': '',
                'case': '',
                'gear': '',
                'pulley': '',
                'differential': '',
                'heat': ''
            },
            datetimeRange: null
        }
    },
    mounted() {
        console.log('');
        const arr = this.$store.state.choose.split('+');
        this.deviceNum = arr[0];
        this.stationNum = arr[1];
        // status
        this.$watch("status", (newVal, oldVal) => {
            this.tableData = [];
            this.setProblemsCount();
            this.setTableData();
        });
        // department
        this.$watch("department", (newVal, oldVal) => {
            this.tableData = [];
            this.setProblemsCount();
            this.setTableData();
        });
        // search
        this.$watch("search", (newVal, oldVal) => {
            this.tableData = [];
            this.setProblemsCount();
            this.setTableData();
        });
        // page
        this.$watch("page", (newVal, oldVal) => {
            this.tableData = [];
            this.setTableData();
        });
        // datetimeRange
        this.$watch("datetimeRange", (newVal, oldVal) => {
            this.tableData = [];
            this.setProblemsCount();
            this.setTableData();
        });
        // 
        this.departOptions.push({
            value: '',
            label: this.USER_VALUE[this.$store.state.plant]
        });
        this.setProblemsCount();
        this.setTableData();
    },
    methods: {
        // 
        toMap() {
            this.$store.state.problemPage = 'all';
            this.$store.commit('saveStateToStorage');
            this.$emit('toMap');
            this.$router.push(this.$route.path.replace(new RegExp("/problems$"), ""));
        },
        /**
         * count
         */
        setProblemsCount() {
            // 
            let startDatetime = null;
            let endDatetime = null;
            if (this.datetimeRange != null) {
                startDatetime = moment(this.datetimeRange[0]).format('YYYY-MM-DD HH:mm:ss');
                endDatetime = moment(this.datetimeRange[1]).format('YYYY-MM-DD HH:mm:ss');
            }

            if (this.$store.state.problemPage == 'device') {
                // 
                new Promise((resolve, reject) => {
                    this.$axiosInstance.get("/plant/problems/count", {
                        params: {
                            plant: this.$store.state.plant,
                            deviceNum: this.deviceNum,
                            stationNum: this.stationNum,
                            status: this.status == 'all' ? null : this.status,
                            search: this.isEmpty(this.search) ? null : this.search,
                            department: this.department == 'all' ? null : this.department,
                            startDatetime: startDatetime,
                            endDatetime: endDatetime
                        }
                    }).then(function (response) {
                        resolve(response.data['count']);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.count = data;
                })
            } else if (this.$store.state.problemPage == 'all') {
                // 
                new Promise((resolve, reject) => {
                    this.$axiosInstance.get("/plant/problems/count", {
                        params: {
                            plant: this.$store.state.plant,
                            status: this.status == 'all' ? null : this.status,
                            search: this.isEmpty(this.search) ? null : this.search,
                            department: this.department == 'all' ? null : this.department,
                            startDatetime: startDatetime,
                            endDatetime: endDatetime
                        }
                    }).then(function (response) {
                        resolve(response.data['count']);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.count = data;
                })
            } else if (this.$store.state.problemPage == 'unmatch') {
                // 
                new Promise((resolve, reject) => {
                    this.$axiosInstance.post("/plant/problems/unmatch/count", {
                        plant: this.$store.state.plant,
                        status: this.status == 'all' ? null : this.status,
                        search: this.isEmpty(this.search) ? null : this.search,
                        department: this.department == 'all' ? null : this.department,
                        // +
                        allDevice: this.buildAllDeviceNumAndStationNum(),
                        startDatetime: startDatetime,
                        endDatetime: endDatetime
                    }).then(function (response) {
                        resolve(response.data['count']);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.count = data;
                })
            }
        },
        /**
         * 
         */
        setTableData() {
            // 
            let startDatetime = null;
            let endDatetime = null;
            if (this.datetimeRange != null) {
                startDatetime = moment(this.datetimeRange[0]).format('YYYY-MM-DD HH:mm:ss');
                endDatetime = moment(this.datetimeRange[1]).format('YYYY-MM-DD HH:mm:ss');
            }

            if (this.$store.state.problemPage == 'device') {
                new Promise((resolve, reject) => {
                    this.$axiosInstance.get("/plant/problems", {
                        params: {
                            plant: this.$store.state.plant,
                            deviceNum: this.deviceNum,
                            stationNum: this.stationNum,
                            status: this.status == 'all' ? null : this.status,
                            page: this.page,
                            size: 5,
                            search: this.isEmpty(this.search) ? null : this.search,
                            department: this.department == 'all' ? null : this.department,
                            startDatetime: startDatetime,
                            endDatetime: endDatetime
                        }
                    }).then(function (response) {
                        resolve(response.data);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.tableData = data;
                })
            } else if (this.$store.state.problemPage == 'all') {
                // 
                new Promise((resolve, reject) => {
                    this.$axiosInstance.get("/plant/problems", {
                        params: {
                            plant: this.$store.state.plant,
                            status: this.status == 'all' ? null : this.status,
                            page: this.page,
                            size: 5,
                            search: this.isEmpty(this.search) ? null : this.search,
                            department: this.department == 'all' ? null : this.department,
                            startDatetime: startDatetime,
                            endDatetime: endDatetime
                        }
                    }).then(function (response) {
                        resolve(response.data);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.tableData = data;
                })
            } else if (this.$store.state.problemPage == 'unmatch') {
                // 
                new Promise((resolve, reject) => {
                    this.$axiosInstance.post("/plant/problems/unmatch", {
                        plant: this.$store.state.plant,
                        status: this.status == 'all' ? null : this.status,
                        page: this.page,
                        size: 5,
                        search: this.isEmpty(this.search) ? null : this.search,
                        department: this.department == 'all' ? null : this.department,
                        // +
                        allDevice: this.buildAllDeviceNumAndStationNum(),
                        startDatetime: startDatetime,
                        endDatetime: endDatetime
                    }).then(function (response) {
                        resolve(response.data);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(data => {
                    this.tableData = data;
                })
            }
        },
        // page 
        setPage(val) {
            this.page = val - 1;
        },
        // root
        handleFinished(id, index) {
            this.$confirm('', '', {
                confirmButtonText: '',
                confirmButtonClass: 'redClass',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                new Promise((resolve, reject) => {
                    this.$axiosInstance.put("/plant/problem", {}, {
                        params: {
                            id: id,
                            status: 'unfinished'
                        }
                    }).then(function (response) {
                        resolve(response.data);
                    }).catch(function (error) {
                        reject(error);
                    })
                }).then(() => {
                    this.tableData[index]['status'] = '';
                })
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        // 
        handleReview(id, index) {
            if (this.$store.state.role == 'root' || (this.$store.state.role == 'local' &&
                this.$store.state.plant == this.$store.state.user)) {
                this.$confirm('', '', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '',
                    confirmButtonClass: 'greenClass',
                    cancelButtonText: '',
                    cancelButtonClass: 'redClass',
                    type: 'warning'
                }).then(() => {
                    new Promise((resolve, reject) => {
                        this.$axiosInstance.put("/plant/problem", {}, {
                            params: {
                                id: id,
                                status: 'finished'
                            }
                        }).then(function (response) {
                            resolve(response.data);
                        }).catch(function (error) {
                            reject(error);
                        })
                    }).then(() => {
                        this.tableData[index]['status'] = '';
                    })
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                }).catch(action => {
                    if (action == 'cancel') {
                        new Promise((resolve, reject) => {
                            this.$axiosInstance.put("/plant/problem", {}, {
                                params: {
                                    id: id,
                                    status: 'unfinished'
                                }
                            }).then(function (response) {
                                resolve(response.data);
                            }).catch(function (error) {
                                reject(error);
                            })
                        }).then(() => {
                            this.tableData[index]['status'] = '';
                        })
                        this.$message({
                            type: 'error',
                            message: '!'
                        });
                    } else if (action == 'close') {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    }
                });
            } else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }
        },
        // 
        handleUnfinished(id, isNeedHelp, index) {
            if (this.$store.state.role == 'root') {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    confirmButtonClass: 'greenClass',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    new Promise((resolve, reject) => {
                        this.$axiosInstance.put("/plant/problem", {}, {
                            params: {
                                id: id,
                                status: 'finished'
                            }
                        }).then(function (response) {
                            resolve(response.data);
                        }).catch(function (error) {
                            reject(error);
                        })
                    }).then(() => {
                        this.tableData[index]['status'] = '';
                    })
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
            else if (this.$store.state.role == 'local' &&
                this.$store.state.plant == this.$store.state.user) {
                // 
                if (isNeedHelp == '') {
                    this.$confirm('', '', {
                        confirmButtonText: '',
                        confirmButtonClass: 'greenClass',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        new Promise((resolve, reject) => {
                            this.$axiosInstance.put("/plant/problem", {}, {
                                params: {
                                    id: id,
                                    status: 'finished'
                                }
                            }).then(function (response) {
                                resolve(response.data);
                            }).catch(function (error) {
                                reject(error);
                            })
                        }).then(() => {
                            this.tableData[index]['status'] = '';
                        })
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                } else {
                    // 
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'warning'
                    });
                }
            } else if (this.$store.state.role == 'assist' &&
                this.USER_VALUE[this.$store.state.user] == isNeedHelp) {

                this.$confirm('', '', {
                    confirmButtonText: '',
                    confirmButtonClass: 'yellowClass',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    new Promise((resolve, reject) => {
                        this.$axiosInstance.put("/plant/problem", {}, {
                            params: {
                                id: id,
                                status: 'review'
                            }
                        }).then(function (response) {
                            resolve(response.data);
                        }).catch(function (error) {
                            reject(error);
                        })
                    }).then(() => {
                        this.tableData[index]['status'] = '';
                    })
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            } else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }
        },
        // 
        isEmpty(str) {
            if (str == null || str.trim() == "") {
                return true;
            }
            return false;
        },
        // +in sql
        buildAllDeviceNumAndStationNum() {
            let stringBuilder = [];
            this.$store.state.shapes.forEach((shape, key) => {
                stringBuilder.push('(\''
                    + shape['deviceNum']
                    + '\',\''
                    + (this.isEmpty(shape['stationNum']) ? '()' : shape['stationNum'])
                    + '\')');
            });
            return stringBuilder.join(', ');
        }
    }
}
</script>

<!-- element canvas -->
<style>
.body {
    width: calc(100vw - 50px);
    overflow-y: scroll;
}

.body #title {
    margin-bottom: 20px;
}

.body #title .element {
    font-weight: bold;
    font-size: 20px;
    margin: 10px 0px 10px 20px;
}

.body #title #account {
    float: right;
    margin-right: 20px;
    margin-top: 0px;
}

.body #operation .search {
    width: 200px;
    display: inline-block;
    position: relative;
    left: 20px;
}

.body #operation .datetimeRange {
    display: inline-block;
    position: relative;
    left: 30px;
    vertical-align: middle;
}

.body #operation .departFilter {
    width: 140px;
    float: right;
    margin-right: 20px;
    margin-top: 0px;
}

.body #operation .statusFilter {
    width: 140px;
    float: right;
    margin-right: 20px;
    margin-top: 0px;
}

.body #table {
    margin: 20px 20px 20px 20px;
}

.body #page {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

/*  */
.el-pagination.is-background .el-pager li:hover {
    color: #10472D;
}

.el-pagination.is-background .el-pager li:not(.disabled).is-active {
    background-color: #10472D;
}
</style>